<template>
  <div>
    <h1>Switch </h1>
    <p></p>
    <showcomponent
      title=""
      :attributes="['active-color,inactive-color']"
      ctname="Switch"
      :codes="code1"
    >
      <template v-slot:showarea>
        <div class="block_show_div">
          <div class="flex_col">
            <ct-switch
              v-model="value"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </ct-switch>
          </div>
        </div>
      </template>
      <template v-slot:desc>
        <code>v-model</code><code>Boolean</code><code>active-color</code><code>inactive-color</code>
      </template>
    </showcomponent>
    <showcomponent
      title=""
      :attributes="['active-text,inactive-text,active-color,inactive-color']"
      ctname="Switch"
      :codes="code3"
    >
      <template v-slot:showarea>
        <div class="block_show_div">
          <div class="flex_col">
            <ct-switch
              style="margin-bottom: 20px"
              v-model="value2"
              active-text=""
              inactive-text=""
            >
            </ct-switch
            >&nbsp;
            <ct-switch
              style="display: block"
              v-model="value3"
              active-color="#13ce66"
              inactive-color="#ff4949"
              active-text=""
              inactive-text=""
            >
            </ct-switch>
          </div>
        </div>
      </template>
      <template v-slot:desc>
        <code>active-text</code><code>inactive-text</code>
      </template>
    </showcomponent>
    <showcomponent
      title=""
      :attributes="['disabled']"
      ctname="Switch"
      :codes="code2"
    >
      <template v-slot:showarea>
        <div class="block_show_div">
          <div class="flex_col">
            <ct-switch v-model="value1" disabled> </ct-switch>
          </div>
        </div>
      </template>
      <template v-slot:desc>
        <code>v-model</code><code>Boolean</code><code>active-color</code><code>inactive-color</code>
      </template>
    </showcomponent>
    <h3 style="margin: 55px 0 20px; font-size: 22px">Attributes</h3>
    <showpara :parameter="parameter1"></showpara>
    <h3 style="margin: 55px 0 20px; font-size: 22px">Events</h3>
    <showpara :parameter="parameter2"></showpara>
    <h3 style="margin: 55px 0 20px; font-size: 22px">Methods</h3>
    <showpara :parameter="parameter3"></showpara>
  </div>
</template>

<script>
import showcomponent from "../../components/showcomponent";
import showpara from "../../components/showparameter";
export default {
  name: "switch",
  chName: "",
  components: {
    showcomponent,
    showpara,
  },
  data() {
    return {
      value: true,
      value1: false,
      value2: true,
      value3: true,
      code1:
        `
  <el-switch
      v-model="value"
      active-color="#13ce66"
      inactive-color="#ff4949">
  </el-switch>
  <script>
      export default {
        data() {
          return {
            value: true
          }
        }
      };
<` +
        `/` +
        `script>`,
      code2:
        `
  <el-switch
        v-model="value1"
        active-text=""
        inactive-text="">
  </el-switch>
  <el-switch
        style="display: block"
        v-model="value2"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text=""
        inactive-text="">
  </el-switch>
  <script>
    export default {
      data() {
        return {
          value1: true,
          value2: true
        }
      }
    };
<` +
        `/` +
        `script>`,
      code3:
        `
  <el-switch
        v-model="value1"
        disabled>
  </el-switch>
  <el-switch
        v-model="value2"
        disabled>
  </el-switch>
  <script>
        export default {
          data() {
            return {
              value1: true,
              value2: false
            }
          }
        };
<` +
        `/` +
        `script>`,
      parameter1: {
        title: ["", "", "", "", ""],
        contents: [
          ["value / v-model", "", "boolean / string / number", "", ""],
          ["disabled", "", "boolean", "", "false"],
          ["plain", "", "Boolean", "true/false", "false"],
          ["round", "", "Boolean", "true/false", "false"],
          ["circle", "", "Boolean", "true/false", "false"],
          ["size", "", "String", "large/medium/small/mini", "large"],
          ["icon", "", "String", "edit/share/delete/...", "-"],
        ],
      },
      parameter2: {
        title: ["", "", ""],
        contents: [["change", "switch ", ""]],
      },
      parameter3: {
        title: ["", "", ""],
        contents: [["focus", " Switch ", "-"]],
      },
    };
  },
};
</script>

<style scoped>
.flex_col {
  height: 80px;
  color: #8492a6;
}
</style>
