<template>
  <div>
    <h1>Loading </h1>
    <p></p>
    <Showcomponent title="" :codes="code1">
      <template v-slot:desc>
        <div>
          <code>v-loading</code>
        </div>
      </template>
      <template v-slot:showarea>
        <div>
          <div v-loading="loading" class="container"></div>
          <ct-button @click="toggleLoading">/</ct-button>
        </div>
      </template>
    </Showcomponent>

    <Showcomponent title="" :codes="code2">
      <template v-slot:desc>
        <div><code>fullscreen</code></div>
      </template>
      <template v-slot:showarea>
        <div>
          <div v-loading.fullscreen="loading2" class="container"></div>
          <ct-button @click="toggleLoading2"></ct-button>
        </div>
      </template>
    </Showcomponent>

    <Showcomponent title="Options" :codes="code3">
      <template v-slot:desc>
        <div><code>options</code></div>
      </template>
      <template v-slot:showarea>
        <div>
          <div v-loading:[options3]="loading3" class="container"></div>
          <ct-button @click="toggleLoading3">/</ct-button>
        </div>
      </template>
    </Showcomponent>

    <Showcomponent title="" :codes="code4">
      <template v-slot:desc>
        <div><code>this.$loading()</code></div>
        <div>
          <code>target</code><code>fullscreen</code>true<code>target</code>
        </div>
        <div>
          <code>data</code><code>options</code>
        </div>
        <div><code>close()</code></div>
      </template>
      <template v-slot:showarea>
        <div>
          <div ref="loading4" class="container"></div>
          <ct-button @click="toggleLoading4"></ct-button>
        </div>
      </template>
    </Showcomponent>

    <h2>Options</h2>
    <Showparameter :parameter="params"></Showparameter>
  </div>
</template>

<script>
import Showcomponent from "../../components/showcomponent/index.vue";
import Showparameter from "../../components/showparameter/index.vue";
export default {
  name: "loading",
  chName: "",
  data() {
    return {
      params: {
        title: ["", "", "", "", ""],
        contents: [
          ["text", "", "String", "", "loading..."],
          [
            "animaStyle",
            "",
            "String",
            "normal/moon",
            "normal",
          ],
          ["spinner", "", "String", "", ""],
          ["fontColor", "", "String", "RBG/16/", "#008fdb"],
          [
            "bgColor",
            "",
            "String",
            "RBG/16/",
            "rgba(0, 0, 0, 0.3)",
          ],
        ],
      },
      loading: true,
      loading2: false,
      loading3: true,
      options3: {
        text: "",
        animeStyle: "moon",
        bgColor: "rgba(100,100,100,0.5)",
        fontColor: "white",
      },
      code1:
        `  <template>
    <div>
      <div v-loading="loading" class="container"></div>
      <ct-button @click="toggleLoading">/</ct-button>
    </div>
  </template>

  <script>
    export default {
      data() {
        return {
          loading: false,
        };
      },
      methods: {
        toggleLoading() {
          this.loading = !this.loading;
        },
      },
    };
    <` + `/script>`,
      code2:
        `  <template>
    <div>
      <div v-loading.fullscreen="loading2" class="container"></div>
      <ct-button @click="toggleLoading2">/</ct-button>
    </div>
  </template>

  <script>
    export default {
      data() {
        return {
          loading2: false,
        };
      },
      methods: {
        toggleLoading2() {
          this.loading2 = true;
          setTimeout(() => {
            this.loading2 = false;
          }, 2000);
        },
      },
    };
    <` + `/script>`,
      code3:
        `  <template>
    <div>
      <div v-loading:[options3]="loading3" class="container"></div>
      <ct-button @click="toggleLoading3">/</ct-button>
    </div>
  </template>

  <script>
    export default {
      data() {
        return {
          loading3: false,
          options3: {
            text: "",
            animeStyle: "moon",
            bgColor: "rgba(100,100,100,0.5)",
            fontColor: "white",
          },
        };
      },
      methods: {
        toggleLoading3() {
          this.loading3 = !this.loading3;
        },
      },
    };
    </` + `/script>`,
      code4:
        `  <template>
    <div>
      <div ref="loading4" class="container"></div>
      <ct-button @click="toggleLoading4"></ct-button>
    </div>
  </template>

  <script>
    export default {
      data() {
        return {};
      },
      methods: {
        toggleLoading4() {
          let instance = this.$loading({
            fullscreen: false,
            target: this.$refs.loading4,
            data: {
              text: "",
              spinner: "iconfont icon-jiazai",
              fontColor: "white",
              bgColor: "rgba(100,200,100,.4)",
            },
          });
          setTimeout(() => {
            instance.close();
          }, 2000);
        },
      },
    };
    <` + `/script>`,
    };
  },
  methods: {
    toggleLoading() {
      this.loading = !this.loading;
    },
    toggleLoading2() {
      this.loading2 = true;
      setTimeout(() => {
        this.loading2 = false;
      }, 2000);
    },
    toggleLoading3() {
      this.loading3 = !this.loading3;
    },
    toggleLoading4() {
      let instance = this.$loading({
        fullscreen: false,
        target: this.$refs.loading4,
        data: {
          text: "",
          spinner: "iconfont icon-jiazai",
          fontColor: "white",
          bgColor: "rgba(100,200,100,.4)",
        },
      });
      setTimeout(() => {
        instance.close();
      }, 2000);
    },
  },
  components: { Showcomponent, Showparameter },
};
</script>

<style lang="less" scoped>
.container {
  width: 800px;
  height: 200px;
  background-color: @main-color3;
}
</style>
